//загальні стилі на хедер// позиціонування
header {
  min-width:320px;
  min-height: 230px;
}

.header__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-width: 280px;
  min-height: 460px;
  padding: 14px 14px 24px;

  background-image: linear-gradient(75deg, rgba(0, 0, 0, 0.1) 9%, rgba(0, 0, 0, 0.3) 100%),
    url("../images/hero_mob_red@1x.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 10px;


  //для респонсивних фонових зображень
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(75deg, rgba(0, 0, 0, 0.1) 9%, rgba(0, 0, 0, 0.3) 100%),
        url("../images/hero_mob_red@2x.webp");
    }

  @include tablet-only {
    min-width: 704px;
    min-height: 222px;
    padding: 16px;

    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 2%, rgba(0, 0, 0, 0.4) 60%),
      url("../images/hero-bg-tabl@1x.webp");

    //для респонсивних фонових зображень
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 2%, rgba(0, 0, 0, 0.4) 60%),
          url("../images/hero-bg-tabl@2x.webp");
      }
  }
  
  @include desktop {
    max-width: 1216px;
    min-height: 316px;
    padding: 16px 32px;

    background-image: linear-gradient(204deg, rgba(0, 0, 0, 0.1) 2%, rgba(0, 0, 0, 0.3) 60%),
      url("../images/hero_desk_red@1x.webp");

    //для респонсивних фонових зображень
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: linear-gradient(204deg, rgba(0, 0, 0, 0.1) 2%, rgba(0, 0, 0, 0.3) 60%),
        url("../images/hero_desk_red@2x.webp");
    }
  }

}


//Навігація
.header__box-nav {
  @include tablet-and-up {

    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;

  }
}

.header-nav {
  margin-bottom: 8px;

  &__list {
    display: flex;
  }

  &__item:first-child {
    margin-right: 4px;
  }
}

.header-nav__link {
  display: flex;
  justify-content: center;


  padding: 10px 22px;

  line-height: 1.17;
  
  /* font-size: 12px;
  font-weight: 500;
  font-style: normal; */

  background-color: $color-text-white;
  color: $color-text-primary;
  border-radius: 100px;
  min-width: 87px;
  text-transform: uppercase;

  transition: color $transition-duration $transition-timing-function, background-color $transition-duration $transition-timing-function;

  &:hover,
  &:focus {
    background-color: $color-text-white;
    color: $bg-color-accent;
  }


  @include tablet-only {
    height: 38px;
    padding: 12px 20px;
    align-items: center;
  }

  @include desktop {
    height: 42px;
    padding: 14px 24px;
    align-items: center;
  }
}

.header-nav__link--current {
  background-color: $color-text-primary;
  color: $color-text-white;

}


//Форма пошуку
.search-form {

  position: relative;
  width: 235px;
  height: 34px;

  margin-left: 0;
  margin-right: 0;

  @include tablet-only {

    width: 165px;
    height: 38px;
    margin-right: 4px;
  }

  @include desktop {

    width: 187px;
    height: 42px;
    margin-right: 8px;
  }

}

.search-form__input {

  max-width: 235px;
  width: 100%;
  
  border-style: none;
  border-radius: 100px;
  padding: 10px 14px;

  @include tablet-only {
    max-width: 165px;
    height: 38px;
    padding: 11px 16px;
  }

  @include desktop {
    max-width: 187px;
    height: 42px;
    padding: 13px 16px;
  }

}

.search-form__input:focus-visible,
.search-form__btn:focus-visible {
  outline: none;
}

.search-form__btn {

  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-98%, -100%);

  height: inherit;
  padding: 10px;
  cursor: pointer;
  border-style: none;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  background-color: $color-text-white;

  display: inline-flex;
  justify-content: center;
  align-items: center;

}


.search-form__icon {

  transform: scale(1);
  transition: transform $transition-duration $transition-timing-function;

  .search-form:hover &, 
  .search-form:focus & {
    transform: scale(1.5);
  }
 
}


