.logo-link {
  height: 38px;
  display: flex;

  justify-content: center;
  align-items: center;

  @include tablet-and-up {

    display: inline-flex;

  }
}

.logo__icon {
  width: 32px;
  height: 32px;

  @include tablet-only {
    width: 40px;
    height: 40px;
  }

  @include desktop {
    width: 59px;
    height: 59px;
  }
}

.logo__txt {
  width: 150px;
  height: 38px;

  font-weight: 500;
  font-size: 32px;
  line-height: 1.2;

  margin-left: 8px;
  display: flex;
  align-items: center;
  text-align: center;

  color: $color-text-white;

  @include tablet-only {

    font-size: 40px;
    line-height: 1.17;

  }

  @include desktop {

    font-size: 64px;

  }
}